<template>
  <div class="wl-txtlinkhref">
    <div class="h-headtitle">跨端支付，APP跳支付宝/微信小程序支付</div>
    <div class="h-blocks">
      <div class="h-title">介绍</div>
      <div class="h-main">跨端支付场景注意是APP端到支付宝小程序、微信小程序端支付的场景，因为公司定制业务需要！一下是uniapp一端生成的代码！</div>
    </div>

    <div class="h-blocks">
      <div class="h-title">一、APP端跳支付宝小程序支付</div>
      <div class="hb-a">1.APP端准备跳转的代码</div>
      <div v-highlight>
                <pre>
                    <code>
                      //判断操作系统 ios,android
                      let osName = JSON.parse(uni.getStorageSync('systemInfo')).osName;
                      let goUrl = 'pages-orderPay/payForApp/payForApp'; //支付宝小程序处理支付的地址
                      //传的参数，估计具体业务来，但一定要encodeURIComponent
                      let querys = encodeURIComponent(`key=${dparam.orderCode}&paySource=1`);
                      //判断操作系统
                      //特别注意：ios和android的跳转协议不一样
                      // ios:alipay
                      // android:alipays
                      //appId 支付宝小程序id
                      if(osName == 'ios'){
                        plus.runtime.openURL(`alipay://platformapi/startapp?appId=${pageConfig.alipayAPPId}&page=${goUrl}&query=${querys}`,function (res){
                          uni.showToast({
                            icon:'none',
                            title:'请安装支付宝后再进行支付！',
                          })
                        });
                      }else if(osName == 'android'){
                        plus.runtime.openURL(`alipays://platformapi/startapp?appId=${pageConfig.alipayAPPId}&page=${goUrl}&query=${querys}`,function (res){
                          uni.showToast({
                            icon:'none',
                            title:'请安装支付宝后再进行支付！',
                          })
                        });
                      }

                    </code>
                </pre>
      </div>
      <div class="hb-a">2.支付宝小程序接受参数并发起支付</div>
      <div v-highlight>
              <pre>
                  <code>
                    //tradeNo 交易号后端接口会返回
                    my.tradePay({
                        tradeNO: tradeNo,
                        success: function (resultAli) {
                          if (resultAli.resultCode == 9000) {
                            my.showToast({
                              type: 'success',
                              content: '支付成功',
                              duration: 2000,
                            })
                          } else {
                            my.showToast({
                              type: 'fail',
                              content: '支付失败 请重试',
                              duration: 2000,
                            })
                          }
                        },
                        fail: function (res) {
                          my.showToast({ type: 'fail', content: '支付服务异常 请退出重试' })
                        },
                      })
                  </code>
              </pre>
      </div>
    </div>

    <div class="h-blocks">
      <div class="h-title">二、APP端跳微信小程序支付</div>
      <div class="hb-a">1.APP端准备跳转的代码</div>
      <div v-highlight>
                <pre>
                    <code>
                      plus.share.getServices(function(res){
                        let sweixin = null;
                        for(var i=0;i<res.length;i++){
                           var t = res[i];
                           if(t.id == 'weixin'){
                               sweixin = t;
                           }
                        }
                        if(sweixin){
                          sweixin.launchMiniProgram({
                            id: 'gh_xxxxx',  //这里写你的小程序原始id（以gh开头）
                            type: 0,//这里是不同的环境（默认0） 0-正式版； 1-测试版； 2-体验版。 默认值为0。
                            path:`/pages-orderPay/payForApp/payForApp?key=${dparam.orderCode}&paySource=1`//这里是指定页的路径，paySource 1 从下单页 ，2 开通会员
                          });
                        }
                      },function(res){
                        console.log(JSON.stringify(res));
                    });
                    </code>
                </pre>
      </div>
      <div class="hb-a">2.微信小程序接受参数并发起支付</div>
      <div v-highlight>
              <pre>
                  <code>
                    //uniapp支付方法
                    uni.getProvider({
                      service:'payment',
                      success:function (resProveder){
                        //先获取支付服务商
                        let providerName = resProveder.provider[0];
                        uni.requestPayment({
                          provider: providerName,
                          timeStamp: timeStamp,
                          nonceStr: nonceStr, //随机串
                          package: package,
                          signType: signType, //微信签名方式：
                          paySign: paySign, //微信签名
                          success: function (res) {
                            //调回APP
                            //支付成功业务处理
                          },
                          fail: function (err) {
                            //调回APP
                            //支付失败业务处理
                          }
                        });
                      }
                    })
                  </code>
              </pre>
      </div>
    </div>


  </div>
</template>

<script>
export default {
  name: 'wltxtlinkhref',
  data() {
    return {}
  },
  mounted() {
    this.doCode();
  },
  methods: {
    //一进来需要执行的
    doCode() {
      $('.view-code-btn').click(function () {
        $(this).parent().find('.hb-code').slideToggle();
      });
    },
  },
  components: {}
}
</script>
<style scoped>

</style>



